















import Vue from "vue";
import { VueEvent } from "@/types/index";

export default Vue.extend({
  props: {
    item: {
      type: String,
      required: true,
    },
  },
  methods: {
    handleInput(event: VueEvent.Input<HTMLInputElement>) {
      const eventTarget = event.target;
      this.$emit("inputEvent", eventTarget.value);
    },
    addTodo() {
      this.$emit("add");
    },
  },
});
