








































import Vue from "vue";
import TodoInput from "./components/TodoInput.vue";
import TodoListItem from "./components/TodoListItem.vue";
import { VueEvent } from "./types";

const STORAGE_KEY = "vue-todo-ts-v1";
const storage = {
  save(todoItems: Todo[]) {
    const parsed = JSON.stringify(todoItems);
    localStorage.setItem(STORAGE_KEY, parsed);
  },
  fetch(): Todo[] {
    const todoItems = localStorage.getItem(STORAGE_KEY) || "[]";
    const result = JSON.parse(todoItems);
    return result;
  },
};

export interface Todo {
  title: string;
  done: boolean;
}

export interface Point {
  top: number;
  left: number;
}

export default Vue.extend({
  components: { TodoInput, TodoListItem },
  data() {
    return {
      todoText: "",
      todoItems: [] as Todo[],
      pointXY: {
        top: 0,
        left: 0,
      } as Point,
    };
  },
  computed: {
    movePointer(): { top: string; left: string } {
      return {
        top: this.pointXY.top + "px",
        left: this.pointXY.left + "px",
      };
    },
  },
  methods: {
    updateTodoText(value: string) {
      this.todoText = value;
    },
    addTodoItem() {
      const value = this.todoText;
      const todo: Todo = {
        title: value,
        done: false,
      };
      this.todoItems.push(todo);
      storage.save(this.todoItems);
      this.initTodoText();
    },
    initTodoText() {
      this.todoText = "";
    },
    fetchTodoItems() {
      this.todoItems = storage.fetch().sort((a, b) => {
        if (a.title < b.title) {
          return -1;
        }
        if (a.title > b.title) {
          return 1;
        }
        return 0;
      });
      return "hi";
    },
    removeTodoItem(index: number) {
      this.todoItems.splice(index, 1);
      storage.save(this.todoItems);
    },
    toggleTodoItemComplete(todoItem: Todo, index: number) {
      this.todoItems.splice(index, 1, {
        ...todoItem,
        done: !todoItem.done,
      });
      storage.save(this.todoItems);
    },
    changePointer(event: VueEvent.Click) {
      this.pointXY.left = event.x;
      this.pointXY.top = event.y;
    },
    pointerIn() {
      let abc = document.querySelector(".pointer")!;
      abc.classList.remove("noneDisplay");
    },
    pointerOut() {
      let abc = document.querySelector(".pointer")!;
      abc.classList.add("noneDisplay");
    },
  },
  created() {
    this.fetchTodoItems();
  },
});
